body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #15181d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.link {
  font-family: 'Inter', sans-serif;
  color: #ccc;
  margin-right: 100vh;
}

.background-wrapper {
  display: flex;
  justify-content: center;
  min-height: 100vh;
}

.page-title {
  color: #FFFFFF;
  font-family: 'Inter', sans-serif;
  text-transform: none;
  font-size: 19px;
  font-weight: 700;
  margin-bottom: 0;
  text-align: center;
  margin-top: 100px;
  margin-bottom: -80px;
  margin-left: 10px;
}

.color {
  margin-top: 124px;
  position: relative;
  width: 400px;
  height: 350px;
  background-color: #1b1f25;
}

.button-wrap {
  justify-content: center;
  align-items: center;
  margin-left: -25px;
}

.buttons {
  font-family: 'Inter', sans-serif;
  width: 80px;
  height: 40px;
  position: relative;
  margin-top: 40px;
  margin-left: 100px;
  justify-content: center;
  justify-items: center;
  text-align: center;
  margin-left: 30px;
}

.Score {
  color: #FFFFFF;
  font-size: 25px;
  font-family: 'Inter', sans-serif;
  font-weight: 600;
  margin-top: 40px;
}

.noScore {
  font-family: 'Inter', sans-serif;
  font-weight: 600;
}

.wrong {
  color: red;
  font-size: 25px;
  font-family: 'Inter', sans-serif;
  margin-top: 20px;
}

.hint {
  color: #ccc;
  font-size: 9px;
}
